import React from 'react'
import {withRouter } from 'react-router-dom';
import airbus from '../img/airbus.png';
import zf from '../img/zf.png';
import ecosolarbreizh from '../img/ecosolarbreizh.jpg';
import bibus from '../img/bibus.png';

import '../App.css'; 

class Experience extends React.Component {
    
    constructor(props){
        super(props);
        
    }
        
    render() {
        return(
            <div>
                <div id='Experience'>
                    <div className='section-div'>
                        <h2>Experience</h2>
                    </div>
                    <div className='container-experience-centered'>
                        
                        <div style={{"margin-top":"32px","width":"1000px","vertical-align":'top',"marginLeft":"20%"}}>
                                <div className='component-experience2'> 
                                    <img  className="logo"  style={{"width":'100px',"height":'100px'}} src={airbus}/>
                                    <div style={{"margin-left":'20px'}}>
                                        <p className='name-institution'> Airbus</p>
                                        <p className="date">2018 (1 Mois) • Nantes </p> 
                                        <p className="text-component" style={{"font-weight":"bold"}}>Intérim : Data-Analyst  </p>
                                        <p className="text-component" >
                                            Récupération de multiples donnéees pour en dégager des analyses de performance. <br/> 
                                            JavaScript • VBA 
                                        </p>
                                    </div>
                                </div>
                            
                                <div className='component-experience'> 
                                    <img  className="logo"  style={{"width":'100px',"height":'100px'}} src={airbus}/>
                                    <div style={{"margin-left":'20px'}}>

                                        <p className='name-institution'> Airbus</p>
                                        <p className="date">2018 (5 Mois) • Nantes </p> 
                                        <p className="text-component" style={{"font-weight":"bold"}}>Stage Ingénieur : Digitalisation de la chaine de production.  </p>
                                        <p className="text-component" >
                                            Développement en autonomie d'une application web pour le service Innovation et Digitalisation de AIRBUS. <br/> 
                                            Front-end • Back-end • ReactJS • NodeJS
                                        </p>
                                    </div>
                                </div>
          

                           
                                <div className='component-experience2'> 
                                    <img  className="logo" style={{"width":'100px',"height":'100px'}} src={zf}/>
                                    <div style={{"margin-left":'20px'}}>

                                        <p className='name-institution'>Zf Group TRW</p>
                                        <p className="date">2017 (2 Mois) • Brest </p>
                                        <p className="text-component" style={{"font-weight":"bold"}}>Stage Technicien : Conception d'un poste semi-automatisé pour la production de radars automobiles.  </p> 
                                        <p className="text-component" >
                                          Conception d'un nouveau poste semi-automatisé qui intervient dans la production de radars automobiles. Rédaction d'un modèle de cahier des charges général qui permet de standardiser les ressources de l'entreprise et les commandes de postes automatisés.<br/>
                                          Anglais • Cahier des charges • Automobile • Production • Radar
                                        </p>

                                    </div>
                                </div>
                            
                            
     
                                <div className='component-experience' > 
                                    <img  className="logo" style={{"width":'100px',"height":'70px'}} src={ecosolarbreizh}/>
                                    <div style={{"margin-left":'20px'}}>
                                        <p className='name-institution'> 
                                            Ecosolarbreizh</p>
                                        <p className="date">2016 (1 Mois) • Brest </p> 
                                        <p className="text-component" style={{"font-weight":"bold"}}>Stage Ouvrier : Conception de circuits imprimés pour la voiture de course solaire 
                                        <a href="http://www.ecosolarbreizh.com/index.php?page=heol"> HEOL</a>.</p> 
                                        <p className="text-component" >
                                            Découverte de la voiture de course solaire HEOL. Développement de deux circuits imprimés visant à améliorer le circuit de capteur de la voiture.
                                            Electronique • Energie Solaire • Automobile
                                        </p>
                                    </div>
                                </div>

                                <div className='component-experience2' style={{"marginBottom":"40px"}}> 
                                    <img  className="logo" style={{"width":'100px',"height":'100px'}} src={bibus}/>
                                    <div style={{"margin-left":'20px'}}>

                                        <p className='name-institution'>Bibus</p>
                                        <p className="date">2015 (3 semaines) • Brest </p>
                                        <p className="text-component" style={{"font-weight":"bold"}}>Job saisonnier : Agent polyvalent pour le réseau de transport Bibus.  </p> 
                                        <p className="text-component" >
                                            Remise à jour des informations sur les arrêts de bus du réseau.
                                            Agent d'information pour les modifications du réseau. <br/>
                                            Relationel • Transport 
                                        </p>
                                    </div>
                            </div>
                            
                         </div>
                    </div>
                </div>
            </div>
            )
        
    }
        
}


export default Experience;


