import React from 'react'
import {withRouter } from 'react-router-dom';
import Skills from './Skills.jsx';
import Experience from './Experience.jsx';
import Education from './Education.jsx';
import Chart from 'chart.js';

import img from '../img/me.jpg';
import poster from '../img/poster3.png';

import '../App.css'; 

class Home extends React.Component {
    
    constructor(props){
        super(props);
        
    }
    
        
    render() {
        return(
            <div id='Home'>
                <div className='container-presentation'>
                    <div id='header-presentation'>
                            <p className='name'>Pol GRISART</p>
                            <p className='poste'>Elève Ingénieur issu d'une école d'Ingénieurs généraliste</p>
                    </div>
                    <img src={poster} className='poster' /> 
                    <div className='container-left-presentation'>  
                        <p className='text-welcome'> <span id='bienvenue' >B</span>ienvenue sur mon site ! Moi c'est Pol, j'ai 21 ans et suis élève Ingénieur. J'ai créé ce site pour y exposer mon profil et mes projets. Ouvert d'esprit, j'adore apprendre et découvrir de nouvelles choses. Mes domaines de compétences sont le développement informatique en général, la vision par ordinateur et le mangement de projet.</p>
                    </div>

                    
                </div>

                <Education/>
                <Experience/>
                <Skills/>
                <footer style={{"text-align":"center","font-size":"1em"}}>
                    <p>© Pol Grisart 2018</p>
                </footer>
            </div>
            )
    }
        
}


export default Home;


