import React from 'react'
import {withRouter } from 'react-router-dom';
import enib from '../img/enib.png';
import stavanger from '../img/stavanger.png';
import bac from '../img/bac.jpg';

import '../App.css'; 

class Education extends React.Component {
    
    constructor(props){
        super(props);
        
    }
        
    render() {
        return(
            
                <div id='Education'>
                    <div className='section-div'>
                        <h2 >Formation</h2>
                    </div>
                        
                   
                    <div style={{"margin-top":"32px","width":"1000px","vertical-align":'top',"marginLeft":"20%"}}>
                            <div className='component-experience'> 
                                <img  className="logo"  style={{"width":'111px',"height":'100px'}} src={stavanger}/>
                                <div style={{"margin-left":'20px'}}>
                                    
                                    <p className='name-institution'> University of Stavanger</p>
                                    <p className="date">2018 • Stavanger (Norvège) </p> 
                                    <p className="text-component" >
                                        Projet informatique (méthode AGILE) • Management de projet • Vision par ordinateur • Data-Science
                                    </p>
                                </div>
                            </div>

                        <div className='component-experience2'> 
                                <img  className="logo" style={{"width":'100px',"height":'100px'}} src={enib}/>
                                <div style={{"margin-left":'20px'}}>
                                    
                                    <p className='name-institution'>Ecole Nationale d'Ingénieurs de Brest</p>
                                    <p className="date">2016-2018 • Brest </p>
                                    <p className="text-component">Deuxième année de cycle ingénieur. <br/> <span className='description-enib'>(Ecole généraliste à Bac+5. Option en 4ème année en informatique, électronique ou mécatronique)</span> <br/>
                                    Informatique • Traitement et Analyse d'images • Electronique • Mécanique</p> 

                                </div>
                            </div> 

                            <div className='component-experience'> 
                                <img  className="logo" style={{"width":'100px',"height":'100px'}} src={enib}/>
                                <div style={{"margin-left":'20px'}}>
                                    
                                    <p className='name-institution'>Ecole Nationale d'Ingénieurs de Brest</p>
                                    <p className="date">2014-2016 • Brest </p>
                                    <p className="text-component">Cycle préparatoire<br/></p> 

                                </div>
                            </div>

                            <div className='component-experience2'> 
                                <img  className="logo" style={{"width":'100px',"height":'100px'}} src={bac}/>
                                <div style={{"margin-left":'20px'}}>
    
                                    <p className='name-institution'>Lycée Amiral Ronarc'h</p>
                                    <p className="date">2014 • Brest </p>
                                    <p className="text-component">Obtention du baccalauréat scientifique mention bien<br/></p> 

                                </div>
                            </div>
                         
                    </div>
                </div>
            
            )
        
    }
        
}


export default Education;


